<template>
    <div class="big-box">
        <div class="set-box">
            <div class="control-line">
                <span class="control">{{ $t('setcontrol.enablesrc') }}</span>
                <NewSelect :module="module" class="name-input" :realVal="controlData.power_enable" :options="enableOptions"
                    @valChng="modData('power_enable', $event)"></NewSelect>
            </div>
            <div class="pset-src-line">
                <span class="pset-src">{{ $t('setcontrol.psetsrc') }}</span>

                <NewSelect :module="module" class="num-input" :realVal="controlData.pset" :options="psetOptions"
                    @valChng="modData('pset', $event)"></NewSelect>
                <LeetxButton v-if="isShowBtn" class="edit-pset" textColor="#fff" borderRadius="0.5rem" bgcColor="#00c155"
                    width="10vw" height="3.9063vh" fontSize="1.5rem" fontFamily="--SHSC-B" :text="$t('setcontrol.editor')"
                    @click="edit"></LeetxButton>
            </div>
            <div class="too-start-line">
                <span class="too-start">{{ $t('setcontrol.toolstartsrc') }}</span>
                <NewSelect :module="module" class="datetime-input" :realVal="controlData.tool_run" :options="toolOptions"
                    @valChng="modData('tool_run', $event)"></NewSelect>
            </div>

            <div class="sn-line">
                <span class="too-start">{{ $t('setcontrol.barCodeSource') }}</span>
                <NewSelect :module="module" class="datetime-input" :realVal="controlData.sn_src" :options="snOptions"
                    @valChng="modData('sn_src', $event)"></NewSelect>
            </div>
            <div class="backward-mode-line">
                <span class="lang">{{ $t('setcontrol.reversemode') }}</span>
                <NewSelect :module="module" class="lang-input" :realVal="controlData.ccw_mode" :options="bckwrdModes"
                    @valChng="modData('ccw_mode', $event)"></NewSelect>
            </div>
            <div class="backward-speed-line">
                <span class="backward-speed">{{ $t('setcontrol.reversespeed') }}</span>
                <InputVal :module="module" class="torque-input" :minNum="minInputSpeed" :maxNum="maxInputSpeed"
                    v-model:isDirty="isDirty" v-model:sensorValue="controlData.ccw_speed" uname="angle_speed">
                </InputVal>
            </div>
            <div class="max-torque-line">
                <span class="backward-speed">{{ $t('setcontrol.fwrdLimit') }}</span>
                <InputVal :module="module" class="torque-input" :minNum="1" :maxNum="maxInputTorque"
                    v-model:isDirty="isDirty" :sensorValue="controlData.ccw_limit"
                    @updateData="modData('ccw_limit', $event)" uname="torque">
                </InputVal>
            </div>
            <div class="person-device-line">
                <span class="person-device">{{ $t('setcontrol.ergonomicstop') }}</span>
                <LeetxSwitch :module="module" class="person-device-input" :value="controlData.human_stop"
                    v-model="controlData.human_stop" :activeVal="1" :staticVal="0" :height="2"></LeetxSwitch>
            </div>

            <div class="job_pset_mode-line">
                <span class="job_pset_mode">{{ $t('setcontrol.modeSelect') }}</span>
                <NewSelect :module="module" class="job_pset_mode-input" :realVal="controlData.job_pset_mode"
                    :options="{ 'job': 1, 'pset': 0 }" @valChng="modData('job_pset_mode', $event)"></NewSelect>
            </div>
            <div class="job_enable-line">
                <span class="job_enable">{{ $t('setcontrol.jobEnabled') }}</span>
                <LeetxSwitch :module="module" class="job_enable-input" :value="controlData.job_enable"
                    v-model="controlData.job_enable" :activeVal="1" :staticVal="0" :height="2"></LeetxSwitch>
            </div>

            <div class="job-src-line">
                <span class="job-src">{{ $t('setcontrol.jobSource') }}</span>
                <NewSelect :module="module" class="job-num-input" :realVal="controlData.job_src" :options="jobOptions"
                    @valChng="modData('job_src', $event)"></NewSelect>
            </div>
            <!-- <div class="press-start-line">
                <span class="press-start">{{$t('setcontrol.pushtostart')}}</span>
                <NewSelect class="speed-input" :realVal="controlData.press_run" :options="{'选项1':0,'选项2':1,'选项3':2,'选项4':3}" @valChng="modData('press_run',$event)"></NewSelect>
            </div> -->

            <!-- <div class="err-reset-line">
                <span class="angle-unit">{{$t('setcontrol.errorreset')}}</span>
                <NewSelect class="angle-input" :realVal="controlData.result_reset" :options="{'选项1':0,'选项2':1,'选项3':2,'选项4':3}" @valChng="modData('result_reset',$event)"></NewSelect>
            </div> -->
            <LeetxButton :enablerun="false" :module="module" class="confirm-box" textColor="#fff" bgcColor="#00c155"
                width="30.125rem" height="3.75rem" fontSize="2.25rem" fontFamily="--SHSC-B" :text="$t('confirm.confirm')"
                @click="submitControlData"></LeetxButton>

        </div>
    </div>
</template>
<script>
import { messageApi } from '../message/message.js'
import InputVal from '../input/InputVal.vue'
import LeetxSwitch from '../switch/LeetxSwitch.vue'
import { devCfgCtrlSrcGetApi, devCfgCtrlSrcSetApi, busSnCfgUpload } from '../../api/device/device'
import NewSelect from '../select/NewSelect.vue'
import cnfrmWin from '../../dialogs/common/confirm/cnfrm.js'
import LeetxButton from '../controls/LeetxButton.vue'
import langvue from '../../plugins/langVue.js'
export default {
    components: {
        NewSelect,
        LeetxButton,
        InputVal,
        LeetxSwitch,
    },
    data() {
        return {
            module: "DEV_CONTROL",
            isDirty: false,
            col: 11,
            row: 7,
            width: 85.625,
            height: 23.6719,
            // 矩阵
            matrix: undefined,
            prgrmNum: 2,
            controlData: {
                power_enable: 0x00,
                pset: 0x01,
                tool_run: 0x01,
                ccw_mode: 0,
                ccw_speed: 0,
                ccw_limit: 0,
                press_run: 0,
                result_reset: 0,
                human_stop: 0,
                // max_torque: 0,
                job_src: 0x01,
                job_pset_mode: 0,
                job_enable: 0,
                sn_src: 3
            },
            // enableOptions:{'默认使能':0x00,'触摸屏':0x01,'IO':0x02,'现场总线':0x03,'OP开放协议':0x04},
            enableOptions: { '默认使能': 0x00, 'IO': 0x02, '现场总线': 0x03, 'OP开放协议': 0x04 },
            psetOptions: { '触摸屏': 0x01, 'IO': 0x02, '现场总线': 0x03, 'OP开放协议': 0x04 },
            jobOptions: { '触摸屏': 0x01, 'IO': 0x02, '现场总线': 0x03, 'OP开放协议': 0x04 },
            toolOptions: { '触摸屏': 0x01, 'IO': 0x02, '现场总线': 0x03, 'OP开放协议': 0x04, '扳机': 0x10, '按压启动': 0x11 },
            bckwrdModes: { '禁用': 0, '单次点击反转一次': 1, '单次点击永远反转': 2, '两次点击反转一次': 3, '两次点击永远反转': 4 },
            global_control_data: '',
            isShowBtn: false,
            need_check_edit: false,
        }
    },
    methods: {
        edit() {
            if (this.controlData.pset == 3) {
                this.$router.push({ path: '/setup/network/totalline' })
                this.need_check_edit = false
            }
            if (this.controlData.pset == 5) {
                this.$router.push({ path: '/setup/network/sn' })
                this.need_check_edit = false
            }
        },
        tbval(i, j) {
            if (i == 0 && j != 0) {
                return j
            }
            if (j == 0 && i != 0) {
                return (i - 1) * 30 + 1
            }
            return ""
        },
        initMatrixVal() {
            this.matrix = []
            for (let i = 0; i < this.row; i++) {
                this.matrix.push([])
                for (let j = 0; j < this.col; j++) {
                    this.matrix[i].push(false)
                }
            }
        },
        chgColor(i, j) {
            if (i == 0 || j == 0) return
            this.matrix[i][j] = !this.matrix[i][j]
        },
        getUnit(name) {
            return this.$store.state.sensors[name]
        },
        submitControlData() {
            devCfgCtrlSrcSetApi(this.controlData).then(() => {
                let msg = langvue.global.t('setbase.success')
                messageApi.success([msg])
                this.isDirty = false
                this.getControlData()
            }).catch(() => {
                let msg = langvue.global.t('setbase.err')
                messageApi.error([msg])
            });
            this.need_check_edit = false
        },
        modData(key, val) {
            if (key == "ccw_speed" || key == "ccw_limit") {
                this.controlData[key] = val.sensorValue
                return
            }
            this.$bus.emit('updateControlSetting')
            this.controlData[key] = val
        },
        // 获取数据
        getControlData() {
            devCfgCtrlSrcGetApi().then((result) => {
                if (result.status == 0) {
                    this.need_check_edit = true,
                        this.controlData = result.data
                    if (this.controlData.pset == 3 || this.controlData.pset == 5) {
                        this.isShowBtn = true
                    } else {
                        this.isShowBtn = false
                    }
                    this.global_control_data = JSON.stringify(this.controlData)
                    // 设置是不是可以使用激活按钮
                    this.$store.state.f_status.power_enable = this.controlData.power_enable
                    this.$store.state.f_status.tool_run = this.controlData.tool_run
                }
            }).catch(() => {
            });
        },
        //获取Sn数据
        getSnData() {
            busSnCfgUpload().then((result) => {
                if (result.status == 0) {
                    this.need_check_edit = true,
                        this.newSnData = result.data
                    this.global_sn_data = JSON.stringify(this.newSnData)
                    if (("CharMap" in this.newSnData) == false) {
                        this.newSnData.CharMap = [];
                    }
                    if (("Direction" in this.newSnData) == false) {
                        this.newSnData.Direction = 0;
                    }
                    if (("VerifySize" in this.newSnData) == false) {
                        this.newSnData.VerifySize = 0;
                    }
                    if (("CharVerify" in this.newSnData) == false) {
                        this.newSnData.CharVerify = 0;
                    }
                }
                if (result.status == 9106) {
                    let msg = this.$t('settable.failedToObtainSN')
                    messageApi.error([msg])
                }
            })
        },
        initOptions() {
            let defaultEnable = this.$t('setcontrol.defaultEnable')
            let touchscreen = this.$t('setcontrol.touchscreen')
            let totalline = this.$t('setcontrol.totalline')
            let dvlpmntAgrmnt = this.$t('setcontrol.dvlpmntAgrmnt')
            let io = this.$t('setcontrol.io')
            let trigger = this.$t('setcontrol.trigger')
            let pushStart = this.$t('setcontrol.pushStart')
            let nouse = this.$t('setcontrol.nouse')
            let oneOne = this.$t('setcontrol.oneOne')
            let oneForever = this.$t('setcontrol.oneForever')
            let twoOne = this.$t('setcontrol.twoOne')
            let twoForever = this.$t('setcontrol.twoForever')
            let ioReverse = this.$t('setcontrol.ioReverse')
            let filedBus = this.$t('setcontrol.filedBus')
            let barcodeScanner = this.$t('setcontrol.barcodeScanner')
            let barCode = this.$t('setcontrol.barCode')
            let notEnabled = this.$t('setcontrol.notEnabled')
            //下拉框的选项，会覆盖上面的值
            this.enableOptions = { [defaultEnable]: 0x00, [io]: 0x02, [totalline]: 0x03, [dvlpmntAgrmnt]: 0x04 },
                this.psetOptions = { [touchscreen]: 0x01, [io]: 0x02, [totalline]: 0x03, [dvlpmntAgrmnt]: 0x04, [barCode]: 0x05,'Web Api': 0x06  },
                this.toolOptions = { [touchscreen]: 0x01, [io]: 0x02, [totalline]: 0x03, [trigger]: 0x10, [pushStart]: 0x11 },
                this.snOptions = { [notEnabled]: 0x00, [totalline]: 0x03, [dvlpmntAgrmnt]: 0x04, [barcodeScanner]: 0x05,'Web Api': 0x06  },
                this.bckwrdModes = { [nouse]: 0, [oneOne]: 1, [oneForever]: 2, [twoOne]: 3, [twoForever]: 4, [ioReverse]: 0x05, [filedBus]: 0x06 }
        },
        checkDataChange() {
            if (this.controlData.pset == 3 || this.controlData.pset == 5) {
                this.isShowBtn = true
            } else {
                this.isShowBtn = false
            }

        },
        addSnData() {
            let Char = 'AB'
            let Pset = 1
            this.newSnData.CharMap.push({ Char, Pset })
            let verifyBit = []
            for (let i = 0; i < this.newSnData.CharMap.length; i++) {
                verifyBit.push(this.newSnData.CharMap[i].Char)
            }
            this.countCharNum()
        },
        countCharNum(char) {
            let num = 0
            this.newSnData.CharMap.forEach(item => {
                if (item["Char"] == char) {
                    num = num + 1
                }
            })
            return num
        },
        checkRepeatChar() {
            let isRepeat = false
            this.newSnData.CharMap.forEach(item => {
                isRepeat = this.countCharNum(item["Char"]) > 1
            })
            return isRepeat
        },
        delFromArr(i) {
            if (this.newSnData.CharMap.length == 0) { return }
            this.newSnData.CharMap.splice(i - 1, 1)
        },

    },
    computed: {
        tdHeight() {
            return (this.height / this.row) + "vh"
        },
        tdWidth() {
            return (this.width / this.col) + "vw"
        },
        maxInputTorque() {
            let max = this.$store.state.g_status.sensor_max * 1.25;
            return isNaN(max) ? 20 : max;
        },
        minInputSpeed() {
            return this.$store.state.g_status.speed_max * 0.005;
        },
        maxInputSpeed() {
            return this.$store.state.g_status.speed_max
        },
        charLen() {
            return 180 - this.newSnData.StartBit
        },
    },
    created() {
        this.initOptions()
        this.initMatrixVal()
        // this.getSnData()
        this.getControlData()
    },
    beforeRouteLeave(to, from, next) {
        let global_control_unchanged = this.global_control_data == "" || this.global_control_data == JSON.stringify(this.controlData)
        if (global_control_unchanged && this.isDirty == false || this.$store.state.g_status.busy || this.need_check_edit == false) {
            next()
            console.log("Enter")
        } else {
            // console.log("this.global_sn_data", this.global_sn_data)
            // console.log("JSON.stringify(this.newSnData)", JSON.stringify(this.newSnData))
            // console.log("this.global_control_data",this.global_control_data)
            // console.log("JSON.stringify(this.controlData)",JSON.stringify(this.controlData))
            // console.log("global_control_unchanged", global_control_unchanged)
            let title = this.$t('setcontrol.saveSet')
            let confirmText = this.$t('setcontrol.yes')
            let cancelText = this.$t('setcontrol.no')
            let msg = this.$t('setcontrol.isSaveSet')
            cnfrmWin({
                title,
                confirmText,
                cancelText,
                msg
            }).then(() => {
                this.submitControlData()
                next()
            }).catch(() => {
                next()
            });
        }
    },
    watch: {
        controlData: {
            deep: true,
            handler: function () {
                this.checkDataChange()
            }
        },

    }
}
</script>
<style scoped>
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: var(--font1_625) !important;
    color: var(--C3B);
    font-family: var(--FSY);
    font-weight: 500;
}

.big-box {
    position: relative;
    height: 100%;
}

.control-line,
.pset-src-line,
.job-src-line,
.too-start-line,
.backward-mode-line,
.backward-speed-line,
.press-start-line,
.err-reset-line,
.person-device-line,
.job_pset_mode-line,
.job_enable-line,
.limit-len-line,
.sn-line,
.program-map-text,
.max-torque-line {
    position: absolute;
    height: 3.9063vh;
    line-height: 3.9063vh;
    width: 100%;
    top: 2.6961vh;
    color: var(--C3B);
}


.control,
.pset-src,
.job-src,
.too-start,
.lang,
.backward-speed,
.press-start,
.angle-unit,
.person-device,
.job_pset_mode,
.job_enable,
.limit-len,
.bit-dir-text,
.SN-text {
    position: absolute !important;
    left: 7.4375vw;
    top: 60%;
    transform: translateY(-50%);
    font-size: 1.375rem;
}

.SN-text {
    left: 40.4375vw;

}

.name-input,
.job-num-input,
.num-input,
.datetime-input,
.job_pset_mode-input,
.lang-input,
.torque-input,
.angle-input,
.speed-input {
    position: absolute !important;
    right: 12.4375vw;

}

.edit-pset {
    position: absolute;
    right: 1.4375vw;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
}

.person-device-input {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: calc(12.4375vw + 13.5rem);
}

.job_enable-input {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: calc(12.4375vw + 13.5rem);
}


.pset-src-line {
    top: 7.7742vh;
}

.too-start-line {
    top: 12.8125vh;
}

.sn-line {
    top: 17.9035vh;

}

.backward-mode-line {
    top: 22.9035vh;
}

.backward-speed-line {
    top: 27.9035vh;
}

.max-torque-line {
    top: 32.9035vh;
}

.person-device-line {
    top: 37.9035vh;
}

.job_pset_mode-line {
    top: 42.9035vh;
}

.job_enable-line {
    top: 47.9035vh;
}

.job-src-line {
    top: 52.9035vh;
}

.confirm-box {
    position: absolute;
    bottom: 2.5rem;
    left: 50%;
    transform: translateX(-50%);
}
</style>